import blog from '@/api/blog'

export default {
    name: 'create',
    data() {
        return {
            title: '',
            description: '',
            content: '',
            atIndex: false
        }
    },

    methods: {
        async onCreate() {
            let res = await blog.createBlog({title: this.title,  content: this.content, description: this.description, atIndex: this.atIndex})
            console.log(res)
            this.$message.success(res.msg)
            this.$router.push({path: `/detail/${res.data.id}`})
        }
    }
}


